<template>
  <div class="d-flex">
    <navigation />
    <div class="flex-fill" style="background: #EEF2F5; overflow-x: hidden;">
      <div></div>
      <div class="row m-3">
            
          <div class="col-3">
            <a class="btn" href="/dashboard">
              Overall
            </a>
            <a class="btn" href="/performance">
              Response
            </a>
            <a class="btn btn-primary" href="/performance">
              Agent Performance
            </a>
            
          </div>
          <div class="col-3">
            <multiselect
              class
              v-model="agent"
              label="name"
              :multiple="true"
              track-by="id"
              :options="agentList"
              :maxHeight="200"
              :show-labels="false"
            ></multiselect>
          </div>
          <div class="col-3">
            <v-date-picker
              mode="range"
              v-model="daterange"
              :masks="{L:'DD-MM-YYYY',input: ['DD/MM/YYYY'],data:['DD/MM/YYYY']}"
            />
          </div>
          <div class="col-3">
            <button
              @click="selected = 'week'"
              class="btn"
              :class="[selectedClass('week')]"
            >This Week</button>
            <button
              @click="selected = 'month'"
              class="btn"
              :class="[selectedClass('month')]"
            >This Month</button>
          
        </div>
      </div>
      <div class="row m-3">
        <div class="col-12">
          <apex-column-chart
          class="chart"
            type="bar"
            height="350"
            ref="columnChart"
            :options="ColumnOptions"
            :series="ColumnSeries"
            @dataPointSelection="dataPointIndexHandler"
          />
        </div>
      </div>
      <div class="row m-3" v-if="show">
        <div class="col-12">
          <apex-line-chart
          class="chart"
            type="line"
            height="350"
            ref="lineChart"
            :options="LineOptions"
            :series="LineSeries"
          />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Navigation from "@/components/Navigation";
import axios from "axios";
import ApexCharts from "apexcharts";
import VueApexCharts from "vue-apexcharts";
import dayjs from "dayjs";

export default {
  components: {
    Navigation,
    "apex-column-chart": VueApexCharts,
    "apex-line-chart": VueApexCharts
  },
  data() {
    return {
      nameList: [],
      countList: [],
      dayList: [],
      countOneList: [],
      averageList: [],
      selectedAgent: "",
      show: false,
      selected: "month",
      dateStatus: "selected",
      daterange: {
        start: "",
        end: ""
      },
      indate: "",
      outdate: "",
      date: "",
      agent: [],
      agentList: []
    };
  },
  methods: {
    selectedClass(p) {
      return p == this.selected ? "btn-primary" : "";
    },
    async getPreData() {
      this.date = this.selected;
      try {
        const res = await axios
          .get("dashboard/GetAgentPerformanceDashboardPreData")
          .then(res => {
            this.agentList = res.data.agents;
          })
          .finally(() => {
            this.getColumnChartData(this.agentList);
          });
      } catch {
        return null;
      }
    },
    async getColumnChartData(agent) {
      let obj = {
        date: this.date,
        indate:
          this.indate == ""
            ? this.indate
            : dayjs(this.indate).format("YYYY/MM/DD"),
        outdate:
          this.outdate == ""
            ? this.outdate
            : dayjs(this.outdate).format("YYYY/MM/DD"),
        agent: agent
      };

      try {
        let loader = this.$loading.show({
          loader: "dots"
        });
        const res = await axios
          .post("dashboard/GetAgentPerformanceDashboardColumnChartData", obj)
          .then(res => {
            this.nameList = res.data.adminName;
            this.countList = res.data.repliedCount;

            loader.hide();
          })
          .finally(() => {
            this.updateColumnChart();
          });
      } catch {
        return null;
      }
    },
    async getLineChartData(name) {
      let index = this.agentList.findIndex(x => x.name == name);
      let id = this.agentList[index].id;
      console.log(id);
      let obj = {
        date: this.date,
        indate:
          this.indate == ""
            ? this.indate
            : dayjs(this.indate).format("YYYY/MM/DD"),
        outdate:
          this.outdate == ""
            ? this.outdate
            : dayjs(this.outdate).format("YYYY/MM/DD"),
        id: id
      };
      console.log(obj);
      try {
        let loader = this.$loading.show({
          loader: "dots"
        });
        const res = await axios
          .post("dashboard/GetAgentPerformanceDashboardLineChartData", obj)
          .then(res => {
            this.dayList = res.data.day;
            this.countOneList = res.data.count;
            this.averageList = res.data.average;
            console.log("day", this.dayList);
            console.log("count", this.countOneList);
            console.log("average", this.averageList);

            loader.hide();
          })
          .finally(() => {
            this.updateLineChart();
          });
      } catch {
        return null;
      }
    },
    updateColumnChart() {
      this.$refs.columnChart.resetSeries();
      this.$refs.columnChart.updateSeries([
        {
          data: this.countList
        }
      ]);
      this.$refs.columnChart.resetOptions();
      this.$refs.columnChart.updateOptions([
        {
          xaxis: {
            categories: this.nameList
          }
        }
      ]);
    },
    updateLineChart() {
      this.$refs.lineChart.resetSeries();
      this.$refs.lineChart.updateSeries([
        {
          name: "Average of " + this.selectedAgent,
          type: "line",
          data: this.countOneList
        },
        {
          name: "Average of Others",
          type: "area",
          data: this.averageList
        }
      ]);
      this.$refs.lineChart.resetOptions();
      this.$refs.lineChart.updateOptions([
        {
          xaxis: {
            categories: this.dayList
          },
          yaxis: {
            labels: {
              formatter: function(val, index) {
                return val.toFixed(0);
              }
            },
            title: {
              text: this.selectedAgent
            }
          }
        }
      ]);
    },
    update() {
      this.show = false;
      let a;
      if (this.agent.length < 1) {
        a = this.agentList;
      } else {
        a = this.agent;
      }
      if (this.dateStatus == "selected") {
        this.date = this.selected;
        this.indate = "";
        this.outdate = "";
      } else {
        this.date = "";
        this.indate = this.daterange.start;
        this.outdate = this.daterange.end;
      }
      this.getColumnChartData(a);
    },
    dataPointIndexHandler(e, chartContext, config) {
      this.show = true;
      var el = e.target;
      var index = parseInt(el.getAttribute("j"));
      var name = this.ColumnOptions.xaxis.categories[index];
      this.selectedAgent = name;
      this.getLineChartData(name);
    }
  },
  mounted() {
    this.getPreData();
  },
  computed: {
    ColumnOptions() {
      let obj = {
        yaxis: {
          labels: {
            formatter: function(val, index) {
              return val.toFixed(0);
            }
          }
        },
        xaxis: {
          categories: this.nameList
        }
      };
      return obj;
    },
    ColumnSeries() {
      let obj = [
        {
          name: "Answer",
          data: this.countList
        }
      ];
      return obj;
    },
    LineOptions() {
      let obj = {
        colors: ["#3742fa"],
        fill: {
          opacity: [1, 0.1]
        },
        stroke: {
          width: [2, 0]
        },
        xaxis: {
          categories: this.dayList
        },
        yaxis: {
          labels: {
            formatter: function(val, index) {
              return val.toFixed(0);
            }
          },
          title: {
            text: this.selectedAgent
          }
        },
        animations: {
          initialAnimation: {
            enabled: true
          }
        }
      };
      return obj;
    },
    LineSeries() {
      let obj = [
        {
          name: this.selectedAgent,
          type: "line",
          data: this.countOneList
        },
        {
          name: "Average of Others",
          type: "area",
          data: this.averageList
        }
      ];
      return obj;
    }
  },

  watch: {
    selected() {
      this.dateStatus = "selected";
      this.update();
    },
    daterange() {
      this.dateStatus = "daterange";
      this.update();
    },
    channel() {
      this.update();
    },
    agent() {
      this.update();
    }
  }
};
</script>
<style>
.box {
  box-shadow: 0 11.5px 6.5px -3px rgba(0, 0, 0, 0.024),
    0 18.1px 21.9px -3px rgba(0, 0, 0, 0.035),
    0 28px 98px -3px rgba(0, 0, 0, 0.07);
}
.custom-avatar.avatar table[data-v-4ffd1741] {
  width: 40px;
  height: 40px;
}

.custom-avatar.avatar table[data-v-4ffd1741] {
  width: 40px;
  height: 40px;
}
</style>
<style lang="scss" scoped>
.chart {
  background: #fff;
  border-radius: 18px;
  padding: 11px;
}
</style>
